<template>
    <div class="my">
        <hheader :header="hheader">
            <i class="iconfont icon-ren" slot="hleft"></i>
        </hheader>
        <section class="container has-header">
            <div class="top">
                <img class="headerImg" src="http://pic.qiantucdn.com/58pic/28/56/06/24v58PICI1VKpf5dcs2jU_PIC2018.jpg" alt="">
                <h4>章安安</h4>
            </div>
            <ul class="my-ul">
                <li>
                    <span>我的账号</span>
                    <b>章安安</b>
                </li>
                <li>
                    <span>我的电话</span>
                    <b>15758472145</b>
                </li>
            </ul>
            <van-button type="primary" size="large" @click.native="$router.push('/login')">退出登录</van-button>
        </section>
    </div>
</template>

<script>
export default {
    data() {
        return {
            hheader: {
                title: "个人中心",
                back:"true"
            }
        };
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.my {
    .container {
        .top {
            height: rem(495);
            background-color: $color;
            width: 100%;
            padding-top: rem(107);
            margin-top: -10px;
            font-size: rem(40);
            text-align: center;
            color: white;
            .headerImg {
                display: block;
                width: rem(260);
                height: rem(260);
                border-radius: 50%;
                margin: 0 auto;
            }
            h4 {
                font-size: rem(60);
                margin-top: rem(30);
            }
        }
        .my-ul{
            margin-top: rem(40);
            li{
                font-size: rem(45);
                height: rem(130);
                line-height: rem(130);
                padding:0 rem(20);
                border-bottom: 1px solid #e9e9e9;
                b{
                    float: right;
                }
                span{
                    color:#909090;
                }
            }
        }
        .van-button{
            display: block;
            width: 92%;
            margin: rem(250) auto 0;
            background-color: $color;
            color:white;
            border-radius: rem(70);
        }
    }
}
</style>



